<template>
  <div class="sidebar">
    <div class="sidebar-backdrop" @click="closeSidebarPanel" v-if="isPanelOpen"></div>
    <transition name="slide">
      <div v-if="isPanelOpen"
           class="sidebar-panel">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>
<script>
import {useStore} from "../../store";
import {mapActions, mapState} from "pinia";

export default {
  methods: {
    ...mapActions(useStore, ["toggleNav"]),
    closeSidebarPanel() {
      this.toggleNav();
    },
  },
  computed: {
    ...mapState(useStore, ["isNavOpen"]),
    isPanelOpen() {
      return this.isNavOpen;
    }
  },
}
</script>
<style lang="scss">
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.2s ease;
}

.slide-enter,
.slide-leave-to {
  transform: translateX(-100%);
  transition: all 150ms ease-in 0s
}

.sidebar-backdrop {
  background-color: rgba(21, 24, 14, .4);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  cursor: pointer;
  z-index: 2;
}

.sidebar-panel {
  overflow-y: auto;
  background-color: #bdcbf3;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  z-index: 999;
  padding: 3rem 20px 2rem 20px;
  width: 85%;
}
</style>