const required = (value) => {
    if (
        !value ||
        value.length === 0 ||
        (value instanceof Object && !Array.isArray(value) && !value.value)
    ) {
        return 'Ответ на этот вопрос обязателен' // Если нет значения выводим ошибку
    }
    return '';
}

const validEmail = (value) => {
    let emailValue = value.split("@")[0];
    if (/^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/.test(value) && emailValue.length >= 6 && emailValue.length < 32) {
        return '';
    } else {
        return "Введите корректный email адрес";
    }
}

const validTelephone = (value) => {
    if (/((8|\+7)-?)?\(?\d{3}\)?-?\d{1}-?\d{1}-?\d{1}-?\d{1}-?\d{1}-?\d{1}-?\d{2}/.test(value)) {
        return '';
    } else {
        return "Введите корректный телефон";
    }
}


const validName = (value) => {
    if (value.length < 2 || value.length > 255 ) {
        return "Минимум 2 символа, макс 255";
    }
    if (/^[a-zA-ZА-Яа-яёЁ ,.-]+$/i.test(value)) {
        return '';
    } else {
        return "Введите корректное имя.";
    }
}


const validPassword = (value) => {
    if (value.length > 32) {
        return "Длинна пароля меньше 32 символов";
    }
    if (value.length < 6) {
        return "Минимум 6 символов. Пробелы, двоеточия и кавычки не допускаются.";
    }

    if (/^([a-zA-ZА-Яа-яёЁ]+(.+[0-9]|[0-9])|[0-9]+(.+[a-zA-ZА-Яа-яёЁ]|[a-zА-Яа-яёЁ]))+$/g.test(value) && !/[^\wА-Яа-яёЁ-]/iu.test(value)) {
        return '';
    } else {
        return "Введите корректный пароль";
    }
}

const isEmailCode = (value) => {
    if (value.length === 6) {
        return '';
    } else {
        return "Введите шестизначный код";
    }
}


export {required, validEmail, validTelephone, isEmailCode, validPassword, validName};

