<template>
    <p  class="test_form--desc test_form--desc-2">{{question.title}}</p>

  <Question
      :question="question"
      :type="question.type"
      @answer-update="answerUpdate"
      :is-valid="isValid"
  ></Question>

</template>

<script>
import Question from "@/components/Question";

export default {
  name: "QuestionItem",
  props: {
    question: {
      type: Object,
    },
    isValid: {
      type: Boolean
    }
  },
  emits: ["answerSet"],
  data: () => {
    return {
      answer: Object,
    }
  },
  methods: {
    answerUpdate(sentAnswer) {
      this.$emit("answerSet", sentAnswer);
    }
  },
  components: {
    Question
  },
}
</script>

<style scoped>
.test_form--desc-2 {
  font-size: 20px;
  color: #C381D9;
  font-weight: 520;
  padding: 0.5rem 0;
  margin-top: 1rem;
  margin-bottom: 0;
}
</style>