<template>

  <div
      :class="isValid && isValidationFail ? type === 'options' ? question?.options[0].name.length < 2 ? 'failed-validation d-flex gap-5' : 'failed-validation' : 'failed-validation' :  type === 'options' ? question?.options[0].name.length < 2 ? 'd-flex gap-5' : '': ''"
      class="p-2 none-validation">
    <e-input
        v-if="type !== 'options'"
        :data=
            "{  label:question.required ? 'Обязательное поле': '',
       type: type,
       required: question.required,
       clearable: false,
        }"
        :styleConfig="styleConfig" @update:modelValue="sentData"></e-input>
    <template v-else>
      <div v-for="(option, index) in question.options"
           :key="index"
      >
        <ERadio
            :data="{name: question.title}"
            :styleConfig="radio"
            @change="sentData(option.name)"
        >{{ option.name }}
        </ERadio>
      </div>
    </template>
  </div>
  <p class="error-message m-0 p-0" v-show="isValid || this.model.length > 0">{{ errorMessage }}</p>


</template>

<script>
import {inputs} from "@/assets/egalStyles/style";
import ERadio from "@/components/ERadio";
import {required, validEmail} from "@/assets/egal/validations";

export default {
  name: "QuestionCategoryItem",
  props: {
    question: {
      type: Object,
    },
    type: String,
    isValid: {
      type: Boolean,
    }
  },
  components: {
    ERadio
  },
  emits: ["answerUpdate"],
  data: () => {
    return {
      styleConfig: {
        width: '556px',
        height: '41px',
        fontFamily: 'Raleway',
        valueColor: 'black',
        valueFontWeight: '500',
        placeholderColor: '#DEBAEA',
        placeholderDisabledColor: '#DEBAEA',
        placeholderTextAlign: 'center',
        labelColor: 'black',
        labelFontWeight: '400',
        labelTextFontSize: '18px',
        helperTextColor: '#5e6579',
        helperTextFontWeight: '400',
        helperTextFontSize: '14px',
        borderColor: '#DEBAEA',
        borderRadius: '6px',
        backgroundColor: 'white',
        backgroundDisabledColor: 'gray',
        focusBorderColor: '#DEBAEA',
        filledBackgroundColor: 'none',
        filledFontColor: '#DEBAEA',
        iconColor: '#DEBAEA',
        errorColor: 'red',
      },
      radio: {
        // стили для лейбла:
        labelStyle: {
          color: "black",
          fontSize: "18px",
          fontFamily: "Raleway",
          disabled: {
            color: "gray",
            fontSize: "18px",
          },
        },

        // стили для чекбокса:
        borderColor: "#C381D9",
        backgroundColor: "#C381D9",
        hover: {
          borderColor: "#C381D9",
        },
        checked: {
          color: "#C381D9",
          borderColor: "#C381D9",
          backgroundImage: "url(../radio.svg);"
        },
      },
      model: "",
      errorMessage: "",
      validators: {
        email: {
          texts: ["Ваша почта", "Укажите, пожалуйста, свою электронную почту", "Оставьте здесь контакт, с помощью которого мы связывались с вами"],
          email: validEmail
        },
        required: required
      }
    }
  },
  computed: {
    isValidationFail() {
      return this.model.length === 0 && this.question.required === true || this.validateData(0);
    },
    setValidations() {
      let validations = [];

      if (this.validators.email.texts.includes(this.question.title)) {
        validations.push(this.validators.email.email)
      }

      if (this.question.required === true && this.question.required !== "false") {
        validations.push(this.validators.required)
      }

      return validations;

    }
  },
  methods: {

    validateModel(value) {
      if (this.setValidations.length === 0) return;

      let isEmpty = true;
      this.setValidations.forEach(validation => this.errorMessage = validation(value).length > 0 ? validation(value) : this.errorMessage)

      this.setValidations.forEach(validation => {
        if (validation(value).length > 0) isEmpty = false
      })

      if (isEmpty) {
        this.errorMessage = '';
      }

      if (this.errorMessage !== '') {

        if (this.type !== 'options') {
          this.styleConfig.placeholderColor = 'red';
          this.styleConfig.borderColor = 'red';
          this.styleConfig.labelColor = 'red';
          this.styleConfig.focusBorderColor = 'red';
          this.styleConfig.helperTextColor = 'red';
        }

      } else {
        if (this.type !== 'options') {
          this.styleConfig.placeholderColor = '#DEBAEA';
          this.styleConfig.borderColor = '#DEBAEA';
          this.styleConfig.labelColor = '#DEBAEA';
          this.styleConfig.focusBorderColor = '#DEBAEA';
          this.styleConfig.helperTextColor = '#5e6579';
        }
      }
    },

    sentData(value) {

      this.validateModel(value);

      this.model = value;
      let sentAnswer = {
        question: this.question.title,
        required: this.question.required || false,
        page: this.question.page,
        answer: value,
        time: 0,
      }
      this.$emit("answerUpdate", sentAnswer);
    },
    validateData() {

      switch (this.question.title) {
        case 'Ваша почта': {
          return validEmail(this.model)
        }
        case 'Укажите, пожалуйста, свою электронную почту': {
          return validEmail(this.model)
        }
        case 'Оставьте здесь контакт, с помощью которого мы связывались с вами': {
          return validEmail(this.model)
        }
      }
      return false;
    }
  },
  beforeUpdate() {
    if (this.isValid) this.validateModel(this.model)
  }
}
</script>

<style scoped lang="scss">

.error-message {
  font-size: 14px;
  color: red;
}


.test_form--form-box {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.test_form--form-box .input {
  width: 80%;
}

.e-radio {
  padding: 0.2rem 0;
}

span.e-radio--left .text {
  display: block !important;
  gap: 1rem !important;
  flex-direction: column !important;
}

.none-validation {
  border-radius: 6px;
  border: transparent 1px solid;
}

.failed-validation {
  //border: #ee262a 1px solid;
}


@media screen and (max-width: 1000px) {
  .test_form--form-box *:not(p, .e-radio) {
    width: 100%;
  }
}


</style>