import {defineStore} from "pinia";

export const url = process.env.VUE_APP_API_URL || process.env.CLIENT_URL;

export const useStore = defineStore("main", {
    state: (): {
        error: string;
        isNavOpen: any;
    } => {
        return {
            error: "",
            isNavOpen: false,
        };
    },
    actions: {
        toggleNav() {
            this.isNavOpen = !this.isNavOpen;
        },
    },

    getters: {
        NavOpen: (state: { isNavOpen: boolean }) => state.isNavOpen,
        getError: (state: { error: string }) => state.error,
    },
});
